import scroll from '../components/scroll/scroll.vue'
export default {
  name: 'Home',
  components: {
    scroll
  },
  computed: {
    scroll () {
      return this.$refs.scroll
    },
    scroller () {
      return this.$refs.scroll
    }
  },
  data () {
    return {
      tabIdx: 0,
      searchValue: '', // 
      courseList: [],  // 
      perPage: 1,  // 
      perPageSize: 10,  // 
      personInfo: ''  // 
    }
  },
  mounted () {
    let that = this
    that.$nextTick(() => {
      that.getCourseList()
      that.getPersonInfo()
    })
  },
  methods: {
    //  DOM
    refresh () {
      this.scroll.refresh()
    },
    // 
    PullDown () {
      setTimeout(() => {
        this.getCourseList()
        this.refresh()
      }, 500)
    },
    // 
    PullUp () {
      setTimeout(() => {
        this.perPage++
        this.getCourseList()
        this.refresh()
      }, 500)
    },
    // 
    getCourseList () {
      console.log('3333333', localStorage.getItem('token'))
      let that = this
      that.axios({
        method: 'POST',
        url: 'interface/Course/column_list',
        data: {
          per_page: that.perPage,  // 
          limit: that.perPageSize   // 
        }
      }).then((res) => {
        console.log('', res)
        if (res.err_code !== 1) {
          that.showAlert(res.msg)
          return false
        }
        if (that.perPage === 1) {
          that.courseList = res.data
          that.scroller.finishUpdate(true)
        } else {
          that.courseList = that.courseList.concat(res.data)
          if (res.data.length > 0) {
            that.scroller.finishUpdate(true)
          } else {
            that.scroller.finishUpdate(false)
          }
        }
      })
    },
    //   
    getPersonInfo () {
      let that = this
      let data = {
        token: localStorage.getItem('token')
      }
      console.log('4444444', data)
      that.axios({
        method: 'POST',
        url: 'interface/Home_page/my',
        data: data
      }).then((res) => {
        console.log('', res)
        if (res.code !== 1) {
          this.showAlert(res.msg)
          return false
        }
        that.personInfo = res.data
      })
    },
    // 
    inputTyping () {
      let that = this
      console.log(that.searchValue)
      that.axios({
        method: 'POST',
        url: 'interface/Course/course_index_search',
        data: {
          course_title: that.searchValue
        }
      }).then((res) => {
        console.log('', res)
        if (res.err_code !== 1) {
          this.showAlert(res.msg)
          return false
        }
        that.courseList = res.data
      })
    },
    // tab
    toHomePage () {
      this.tabIdx = 0
    },
    toPersonPage () {
      this.tabIdx = 1
      this.getPersonInfo()
    }
  }
}



